import React, { memo } from 'react';

import { Dropdown } from 'primereact/dropdown';
import { MultiSelect } from 'primereact/multiselect';
import { TriStateCheckbox } from 'primereact/tristatecheckbox';

import useProps from '../../hooks/PropsHandlers';
import usePhases from '../../hooks/PhaseHandlers';
import useStores from '../../hooks/DemandsHandlers';
import { useMaterials } from '../../hooks/EstimativeHandlers';
import { useQuery } from 'react-query';
import api from '../../services/api';

function CompoundFilters(props) {
    const { phases } = usePhases()
    const { materials } = useMaterials()
    const { stores } = useStores()
    const {
        lines,
        groups,
        collections,
        stylists,
        designers
    } = useProps()

    const filters = props.periodHooks?.filters
    const filter = props.periodHooks?.setFilters

    const { data: productTags } = useQuery(
        "productTags", 
        () => api.get("tags/types?type=1").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    const { data: processTags } = useQuery(
        "processTags", 
        () => api.get("tags/types?type=2").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )

    return (
        <>
            <MultiSelect optionLabel="name" value={filters?.product_tags} options={productTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filter("product_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de produto"
            />
            <MultiSelect optionLabel="name" value={filters?.process_tags} options={processTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filter("process_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de processo"
            />
            <Dropdown 
                value={filters?.phase}
                options={phases} 
                optionLabel="phase" optionValue="id"
                onChange={(e) => {
                    filter("phase", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Fase"
            />
            <Dropdown 
                value={filters?.store}
                options={stores} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("store", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Loja"
            />
            <Dropdown 
                value={filters?.material}
                options={materials} 
                optionLabel="code" optionValue="id"
                onChange={(e) => {
                    filter("material", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Material"
            />
            <Dropdown 
                value={filters?.line}
                options={lines} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("line", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Linha"
            />
            <Dropdown 
                value={filters?.group}
                options={groups} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("group", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Grupo"
            />
            {filters?.group ? 
                <Dropdown 
                    value={filters?.subgroup}
                    options={
                        groups?.find(group => group.id === filters?.group).subgroups
                    } 
                    optionLabel="name" optionValue="id"
                    onChange={(e) => {
                        filter("subgroup", e.value === undefined ? "" : e.value)
                    }} 
                    showClear={true}
                    placeholder="Subgrupo"
                /> : null
            }
            <Dropdown 
                value={filters?.collection}
                options={collections} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("collection", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Coleção"
            />
            <Dropdown 
                value={filters?.stylist}
                options={stylists} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("stylist", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Estilista"
            />
            <Dropdown 
                value={filters?.designer}
                options={designers} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filter("designer", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Designer"
            />
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb1" 
                    onChange={e => filter("typed", e.value)} 
                    value={filters?.typed} 
                />
                <label htmlFor="cb1" className="p-checkbox-label">Pedido digitado</label>
            </div>
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb2" 
                    onChange={e => filter("erp", e.value)} 
                    value={filters?.erp} 
                />
                <label htmlFor="cb2" className="p-checkbox-label">Cadastrado ERP</label>
            </div>
            <div className='checkfilter bottom'>
                <TriStateCheckbox 
                    inputId="cb3" 
                    onChange={e => filter("generic", e.value)} 
                    value={filters?.generic} 
                />
                <label htmlFor="cb3" className="p-checkbox-label">Produto genérico</label>
            </div>
        </>
    )
}

export default memo(CompoundFilters)