import React, { memo } from 'react';

import { Dropdown } from 'primereact/dropdown';
import { TriStateCheckbox } from 'primereact/tristatecheckbox';
import { MultiSelect } from 'primereact/multiselect';

import useProps from '../../hooks/PropsHandlers';
import useCompounds from '../../hooks/CompoundHandlers';
import { useMaterials } from '../../hooks/EstimativeHandlers';
import { useQuery } from 'react-query';
import api from '../../services/api';

function PhaseFilters(props) {
    const { 
        lines, 
        groups, 
        collections,
        stylists,
        designers
    } = useProps()
    const { compounds } = useCompounds()
    const { materials } = useMaterials()

    const { data: productTags } = useQuery(
        "productTags", 
        () => api.get("tags/types?type=1").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    const { data: processTags } = useQuery(
        "processTags", 
        () => api.get("tags/types?type=2").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )

    const filterPhases = props.filterPhases
    const filters = props.phaseFilters

    return ( 
        <>
            <MultiSelect optionLabel="name" value={filters.product_tags} options={productTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterPhases("product_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de produto"
            />
            <MultiSelect optionLabel="name" value={filters.process_tags} options={processTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterPhases("process_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de processo"
            />
            <Dropdown 
                value={filters.line}
                options={lines} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("line", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Linha"
            />
            <Dropdown 
                value={filters.group}
                options={groups} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("group", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Grupo"
            />
            {filters.group ?
                <Dropdown 
                    value={filters.subgroup}
                    options={
                        groups?.find(group => group.id === filters.group).subgroups
                    } 
                    optionLabel="name" optionValue="id"
                    onChange={(e) => {
                        filterPhases("subgroup", e.value === undefined ? "" : e.value)
                    }} 
                    showClear={true}
                    placeholder="Subgrupo"
                /> : null
            }
            <Dropdown 
                value={filters.collection}
                options={collections} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("collection", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Coleção"
            />
            <Dropdown  
                value={filters.compound}
                options={compounds} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("compound", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Composto"
            />
            {filters.compound &&
                <Dropdown  
                    value={filters.period}
                    options={
                        compounds?.find(compound => compound.id === filters.compound).periods
                    } 
                    optionLabel="name" optionValue="id"
                    onChange={(e) => {
                        filterPhases("period", e.value === undefined ? "" : e.value)
                    }} 
                    showClear={true}
                    placeholder="Período"
                />
            }
            <Dropdown 
                value={filters.material}
                options={materials} 
                optionLabel="code" optionValue="id"
                onChange={(e) => {
                    filterPhases("material", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Material"
            />
            <Dropdown 
                value={filters.stylist}
                options={stylists} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("stylist", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Estilista"
            />
            <Dropdown 
                value={filters.designer}
                options={designers} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    filterPhases("designer", e.value === undefined ? "" : e.value)
                }} 
                showClear={true}
                placeholder="Designer"
            />
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb2" 
                    onChange={e => filterPhases("erp", e.value)} 
                    value={filters?.erp} 
                />
                <label htmlFor="cb2" className="p-checkbox-label">Cadastrado ERP</label>
            </div>
            <div className='checkfilter bottom'>
                <TriStateCheckbox 
                    inputId="cb3" 
                    onChange={e => filterPhases("generic", e.value)} 
                    value={filters?.generic} 
                />
                <label htmlFor="cb3" className="p-checkbox-label">Produto genérico</label>
            </div>
        </>
    )
}

export default memo(PhaseFilters)