import React, { useEffect, useState } from "react";

import "./ViewLogProduct.css";

import {useProduct} from "../../../../hooks/ProductHandlers";

export default function ViewLogProduct(props) {

  const [logs, setLogs] = useState();
  const { logProduct } = useProduct(props.productId);

  useEffect(() => {
    logProduct().then((res) => setLogs(res));
  }, []);
  
  const ColoredLine = ({ color }) => (
    <hr
        style={{
            color: color,
            backgroundColor: color,
            height: 3,
            marginBottom: 20
        }}
    />
);

  return (
    
    <div className="viewLog">
      {logs?.map((log) => 
      <ul className="lista">
        <li><ColoredLine color="rgb(166, 248, 0)"/>{
          log.history_type === "CREATE" ? 
            `
              O produto foi criado por meio do método ${log.history_type} 
              pelo usuário ${log.history_user ? log.history_user.username : "indefinido"} 
              em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + 
              "/" + log.history_date.slice(0, 4) + 
              " às " + log.history_date.slice(11, 16)}.
            ` 
              : 
            `
              O produto foi modificado por meio de ${log.history_type} 
              nos campos listados abaixo pelo usuário ${log.history_user ? log.history_user.username : "indefinido"} 
              em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + 
              "/" + log.history_date.slice(0, 4) + 
              " às " + log.history_date.slice(11, 16)}.
            `
            }</li>
        {log.history_type === "UPDATE" && (typeof log.changes === 'object' && log.changes !== null) ? 
        <ul>
          {Object.entries(log.changes).map((change) => 
            <li className="listaMargin"><span className="coloredTitle">{`${change[0]}`}</span>{`: valor ${change[1].old_value} foi alterado para ${change[1].new_value !== "" ? change[1].new_value : "vazio"}.`}</li>)}
        </ul> : ""}
      </ul>
      )}
    </div>
  )
}
