import React, { useEffect } from 'react';

import ProductCard from '../ProductCard/ProductCard';
import ModalBase from '../shared/ModalBase/ModalBase';
import ViewProductPhase from '../Modals/Phases/ViewProductPhase/ViewProductPhase';
import AddProductPhase from '../Modals/Phases/AddProductPhase/AddProductPhase';

import './PhaseCard.css'
import { usePhaseProducts } from '../../hooks/PhaseHandlers';

export default function PhaseCard(props) {
    const { 
        products, 
        isLoading, 
        isError, 
        setFilters,
        updatePhase,
        phaseOperations
    } = usePhaseProducts(props.phase.id)

    useEffect(() => {
        setFilters(props.filters)       
    }, [props.filters])

    return (
        <div className="phaseCard">
            <div className="phaseCard--title">
                <h3>{props.title.toUpperCase()} | {products ? products.length : 0}</h3>
            </div>
            <div className="phaseCard--body">
                { isLoading ?
                    <>Carregando...</>
                    :
                    products.map((product) => (
                        <ModalBase  key={product.id}
                            caller={<ProductCard key={product.images[0]?.id} data={product} compact hidePhase showDaysOnPhase/>}
                            content={<ViewProductPhase 
                                data={product} 
                                phase={props.phase}
                                allPhases={props.allPhases}
                                phaseOperations={phaseOperations}
                            />}
                            title="Visualizar PRODUTO em PROCESSO"
                            onClose={() => {
                                updatePhase()
                            }}
                        />
                    )
                )}
                {!isLoading &&
                    <ModalBase
                        caller={
                            <div className='FAB'>
                                <h1>+</h1>
                            </div>
                        }
                        content={
                            <AddProductPhase 
                                phase={props.phase} 
                                onClose={() => updatePhase()}
                                addProduct={phaseOperations.add}
                            />
                        }
                        title="Adicionar PRODUTO ao PROCESSO"
                    />
                }
            </div>
        </div>
    )
}