import React, { useEffect, useState } from 'react'
import './style.css'
import 'primeflex/primeflex.css';

import { DataView } from 'primereact/dataview';

import { TextField } from '@mui/material';

import SideBar from '../../components/SideBar/SideBar';
import ProductCard from '../../components/ProductCard/ProductCard';
import ModalBase from '../../components/shared/ModalBase/ModalBase';
import ViewProduct from '../../components/Modals/Products/ViewProduct/ViewProduct';
import AddProduct from '../../components/Modals/Products/AddProduct/AddProduct';

import { isAuthenticated } from '../../services/api';
import useProducts, { getFilteredExport } from '../../hooks/ProductHandlers';
import MobileSidebar from '../../components/MobileSidebar/MobileSidebar';
import { ImportProductModal } from './SpreadSheetModals/ImportProductModal/ImportProductModal';
import { VinculateTagsModal } from './SpreadSheetModals/VinculateTagsModal/VinculateTagsModal';

import { ConfirmDialog } from 'primereact/confirmdialog'; // To use <ConfirmDialog> tag
import { confirmDialog } from 'primereact/confirmdialog'; // To use confirmDialog method

const ProductsPage = () => {
    useEffect(() => {
        isAuthenticated()
    }, [])

    const [lazyParams, setLazyParams] = useState({
        first: 0,
        rows: 12,
        page: 1,
        pageCount: 1
    });
    const [data, setData] = useState({});
    const [mobile, setMobile] = useState(false)

    const { 
        products, 
        isLoading, 
        productFilters,
        filterProducts,
        updateProducts
    } = useProducts()

    useEffect(() => {
        filterProducts("limit", lazyParams.rows)
        filterProducts("offset", lazyParams.first)
    }, [lazyParams])

    useEffect(() => {
        setData(products)
    }, [products])

    
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth < 960) {
                setMobile(true)
            } else setMobile(false)
        }

        window.addEventListener('resize', handleResize)
        handleResize()

        return () => window.removeEventListener('resize', handleResize)
    }, [])

    const itemTemplate = (data, layout) => {
        if (layout === 'grid') {
            return (
                <div className="p-col">
                    <ModalBase
                        caller={<ProductCard data={data} />}
                        content={<ViewProduct productId={data.id} />}
                        title={data.reference}
                        onClose={updateProducts}
                        commentsIcon
                        commentsUrl={`comments?product=${data.id}`}
                    />
                </div>
            );
        }
    }

    const Products = () => {
        return (
            <DataView
                className='p-justify-start'
                value={data?.results ? data.results : data} 
                layout="grid" 
                itemTemplate={itemTemplate} 
                paginator rows={12}
                totalRecords={data?.count ? data.count : data?.length} 
                emptyMessage="Nenhum produto registrado"
                first={lazyParams.first}
                onPage={(e) => setLazyParams(e)}
                lazy
            />
        )
    }

    const PageContent = () => {

        const confirmExport = () => {
            confirmDialog({
                message: `Deseja fazer a exportação dos produtos selecionados (filtros serão considerados)? Note: A quantidade máxima para exportação são 500 produtos.`,
                header: 'Exportar',
                icon: 'pi pi-exclamation-triangle',
                accept: () => getFilteredExport(productFilters),
                reject: () => null,
                acceptLabel: "Sim",
                rejectLabel: "Não",
            });
        }



        return (
            <>
                <ConfirmDialog />
                <div className='main'>
                    <Products/>
                </div>
                <div className='footer'>
                    <ModalBase
                        caller={
                            <div className='FAB footerButton'>
                                <h1>+</h1>
                            </div>
                        }
                        content={<AddProduct />}
                        title="Adicionar Produto"
                    />
                    <div className='FAB footerButton' onClick={() => confirmExport()}>
                        <i className='pi pi-download' style={{fontSize: "1.25rem"}}/>
                    </div>
                    <ImportProductModal />
                    <VinculateTagsModal />
                </div>
            </>
        )
    }

    return (
        <div className='productPage page'>
            {!mobile &&
                <SideBar 
                    productFilters={productFilters}
                    filterProducts={(key, value) => {
                        filterProducts(key, value)
                        setLazyParams(d => ({...d, first: 0}))
                    }} 
                />
            }
            <div className='productPage--body'>
                <div className='header'>
                    <div className='header--content'>
                        {mobile ? 
                            <MobileSidebar 
                                productFilters={productFilters}
                                filterProducts={(key, value) => {
                                    filterProducts(key, value)
                                    setLazyParams(d => ({...d, first: 0}))
                                }} 
                            />
                            :
                            <h2>Estes são os PRODUTOS</h2>
                        }
                        <div className='searchBar'>
                            <TextField id="outlined-basic" 
                            label="Pesquisar" 
                            variant="outlined" 
                            size="small"
                            color="success"
                            onChange={(e) => filterProducts("reference", e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='header--border'></div>
                </div>
                {isLoading ? <h3 id="loadingText">Carregando...</h3> : <PageContent />}
            </div>
        </div>
    )
}

export default ProductsPage