import React, { useState, useRef } from "react";

import { Divider } from "primereact/divider";
import { Button } from "primereact/button";
import { Toast } from "primereact/toast";

import "./MaterialContainer.css";
import Estimative from "../Estimative/Estimative";
import DraftEstimative from "../DraftEstimative/DraftEstimative";

export default function MaterialContainer(props) {
  const [colors, setColors] = useState(props.data.colors);
  const [estimatives, setEstimatives] = useState(props.data.colors);
  const [editing, setEditing] = useState(false);
  const [signal, setSignal] = useState("");
  const [drafts, setDrafts] = useState([]);
  const [count, setCount] = useState(0);
  const toast = useRef();

  const sendSignal = (s) => {
    setSignal(() => {
      setTimeout(() => setSignal(""), 200);
      return s;
    });
  };

  const removeDraft = (index) => {
    setDrafts((d) => [...d].filter((draft) => Number(draft.key) !== index));
  };

  const setDraftData = (id, data) => {
    setDrafts((state) =>
      state.map((d) => {
        if (d.id === id) {
          d.data = data;
        }

        return d;
      })
    );
  };

  const draftData = () => {
    let data = {
      key: count,
      id: count,
      removeDraft: () => removeDraft(count),
      data: {},
    };

    setCount((c) => (c += 1));

    return data;
  };

  const saveDrafts = () => {
    drafts.map((draft) =>
      props.addEstimative({ ...draft.data, color: draft.data.color.id })
    );
    setDrafts([]);
  };

  const changeEstimativeData = (estId, newData) => {
    let changedEstimative;

    let allEsts = JSON.parse(JSON.stringify(estimatives));
    let ests = allEsts.filter((est) => {
      if (est.estimative !== estId) return true;
      else {
        changedEstimative = est;
        return false;
      }
    });

    changedEstimative.assortment = newData.assortment;
    changedEstimative.consumption = newData.consumption;

    setEstimatives([...ests, changedEstimative]);
  };

  const handleSave = () => {
    let totalAssortment = 0;

    drafts.map((draft) => (totalAssortment += draft.data.assortment));
    estimatives.map((est) => (totalAssortment += est.assortment));

    if (totalAssortment === 100 || totalAssortment === 0) {
      setEditing(false);
      sendSignal("save");
      saveDrafts();
    } else {
      toast.current.show({
        severity: "error",
        summary: "Sortimento inválido",
        detail: "A soma dos sortimentos deve equivaler a 100%",
        life: 4000,
      });
    }
  };

  return (
    <div className="mat-container">
      <Toast ref={toast} />
      <Divider align="left" type="dashed">
        <h3 className="mc-title">
          {props.data.material.code} / {props.data.material.description}
          {!editing ? (
            <Button
              id="edit-material"
              className="p-button-rounded p-button-outlined icon-button"
              onClick={() => setEditing(true)}
            >
              <i className="pi pi-pencil" />
            </Button>
          ) : (
            <>
              <Button
                id="save-material"
                className="p-button-rounded p-button-outlined icon-button"
                onClick={handleSave}
              >
                <i className="pi pi-check" />
              </Button>
              <Button
                id="cancel-material"
                className="p-button-rounded p-button-outlined icon-button"
                onClick={() => {
                  setEditing(false);
                  sendSignal("cancel");
                  setDrafts([]);
                }}
              >
                <i className="pi pi-times" />
              </Button>
            </>
          )}
        </h3>
      </Divider>
      <table className="mc-colors">
        <thead>
          <tr>
            <th>Cor</th>
            <th>Consumo</th>
            <th>Sortimento</th>
          </tr>
        </thead>
        <tbody>
          {colors.map((color) => (
            <Estimative
              key={color.color_id}
              data={color}
              matId={props.data.material.id}
              prodId={props.prodId}
              editing={editing}
              materialSignal={signal}
              changeEstimativeData={changeEstimativeData}
            />
          ))}
          {editing &&
            drafts.map((d) => (
              <DraftEstimative
                key={d.key}
                id={d.id}
                matId={props.data.material.id}
                prodId={props.prodId}
                removeDraft={d.removeDraft}
                setDraftData={setDraftData}
                currentColors={colors}
              />
            ))}
        </tbody>
      </table>
      {editing && (
        <Button
          id="add-color"
          className="p-button-rounded p-button-success"
          onClick={() => setDrafts((d) => [...d, draftData()])}
        >
          <i className="pi pi-plus" />
        </Button>
      )}
    </div>
  );
}
