import React, { useState, useEffect } from "react";

import AssignmentTurnedInOutlinedIcon from "@mui/icons-material/AssignmentTurnedInOutlined";
import "./ProductCard.css";

export default function ProductCard(props) {
    const product = props.data;
    const hasImage = props.data.images[0] ? true : false;
    const [imageUrl, setImageUrl] = useState(
        hasImage
        ? props.data.images[0].image
        : "/assets/images/ProdutoSemMidia.svg"
    )

    const cardStyle = {
        backgroundImage: `url(${imageUrl})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "contain",
        backgroundPosition: "center"
    };
        
    useEffect(() => {
        if (product.generic && props.genericImg) {
            setImageUrl(props.genericImg)
        }
    }, [])


    const compactCardStyle = {
        height: "max-content",
    };

    return(
        <div className={`productCard ${props.adaptable ? "adaptable" : ""}`}>
            <div className={`productCard--up ${props.doubleSize ? "doubleSize" : ""}`} 
                style={props.compact && !hasImage ? compactCardStyle : cardStyle}
            >
                { props.typed &&
                    <div className="tag typed">
                        <AssignmentTurnedInOutlinedIcon sx={{width: "1rem", height: "1rem", margin: 0}}/>
                    </div>
                }
                { product.phase && !props.hidePhase &&
                    (
                    <div className='tag phase'>
                        {product.phase} ({product.days_on_phase} dias)
                    </div>
                    )
                }
                { product.registered_ERP ?
                    (
                    <div className='tag ERP'>
                        ERP
                    </div>
                    ) : product.generic ? (
                    <div className='tag generic'>
                        GNR
                    </div>
                    ) : null
                }
                { props.showDaysOnPhase &&
                    (<div className='tag days'>
                        <p>{product.days_on_phase}</p>
                    </div>)
                }
            </div>
            <div className="productCard--down">
                {product.description &&
                    (
                    <div className='description'>
                        {product.description}
                    </div>
                    )
                }
                <div className='reference'>
                    <p>REF: {product.reference}</p>
                </div>
            </div>
        </div>
  );
}
